@import "../../fonts/circular-std/stylesheet.css";
@import "../../fonts/newyork/stylesheet.css";
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "New York" !important;
  font-weight: 600;
}

.text-orange {
  color: #EE9D3E !important;
}

.product-showcase::before {
  background: url("../../img/green-leaf-2.png");
  background-position: 0;
  background-repeat: no-repeat;
  background-size: contain;
  content: "";
  height: 15%;
  left: -0.5%;
  position: absolute;
  top: 2%;
  width: 17%;
  z-index: 99;
}

.rose-shear-butter::after {
  background: url("../../img/green-leaf.png");
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: contain;
  bottom: -80px;
  content: "";
  height: 200px;
  left: -120px;
  position: absolute;
  width: 280px;
}

.home_mini_cart {
  display: flex;
  margin-top: 10px !important;
}
.home_mini_cart .home_quantity {
  width: 110px;
  margin-right: 15px;
}
.home_mini_cart .home_quantity input[type=number]::-webkit-inner-spin-button {
  opacity: 1;
}
.home_mini_cart .single_add_to_cart_button {
  background: #1a202c;
}

.home-best-seller-area::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 470px;
  left: 0;
  bottom: 0;
  background-color: #F7F1EA;
}