.home_mini_cart{
    display: flex;
    margin-top: 10px !important;
    
    .home_quantity{
        width: 110px;
        margin-right: 15px; 
        
        input[type=number]::-webkit-inner-spin-button{
            opacity: 1;
        }
    }
    
    .single_add_to_cart_button{
        background: #1a202c;
    }
}

.home-best-seller-area{
    &::before{
        position: absolute;
        content: '';
        width: 100%;
        height: 470px;
        left: 0;
        bottom: 0;
        background-color: #F7F1EA;
    }
}